import { Geist, Geist_Mono } from "next/font/google";
import "./globals.css";
import Head from 'next/head';
import { LanguageProvider } from '../contexts/LanguageContext';
import ConditionalFooter from '../components/ConditionalFooter';

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

export const metadata = {
  metadataBase: new URL('https://blueera.ai'),
  title: "Blue Era",
  description: "Social platform for AI enthusiasts",
  openGraph: {
    title: "Blue Era - Social platform for AI enthusiasts",
    description: "Join the community of AI enthusiasts on Blue Era",
    images: [
      {
        url: '/logo.png',
        width: 600,
        height: 400,
        alt: "Blue Era Logo",
      },
    ],
    type: "article",
    locale: "en_US",
  },
  twitter: {
    card: "summary_large_image",
    title: "Blue Era - Social platform for AI enthusiasts",
    description: "Join the community of AI enthusiasts on Blue Era",
    images: '/logo.png',
  },
};

export default function RootLayout({ children }) {
  return (
    <html lang="en">
      <head>
        {/* Additional meta tags for better social sharing */}
        <meta property="og:url" content={process.env.NEXT_PUBLIC_BASE_URL || 'http://localhost:3000'} />
        <meta name="twitter:site" content="@blueera" />
      </head>
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased`}>
        <LanguageProvider>
          {children}
        </LanguageProvider>

        <ConditionalFooter />
      </body>
    </html>
  );
}